import React from 'react';

import {TextGroup, Important, Title, SubTitle, Line} from '../../components/TextUtils/TextUtils.js';

var version = "3.0.0";

var UpdateTitle = () => {
    return "TwitchLink " + version;
};

var UpdateNote = () => {
    return (
        <div>
            <SubTitle>업데이트 노트</SubTitle>
            <div style={{padding: "30px 10px"}}>
                <TextGroup>
                    <Title>대규모 업데이트</Title>
                    <Line/>
                    [일반]
                    <br/>
                    <br/>
                    <Important>기존 TwitchLink 어플리케이션의 핵심 부분을 모두 재설계하였습니다.</Important>
                    <br/>
                    메모리 사용을 최적화하였습니다.
                    <br/>
                    소프트웨어 최적화 및 안정화를 진행하였습니다.
                    <br/>
                    <Important>명령어 지원을 추가하였습니다. (명령어를 사용해 TwitchLink를 제어할 수 있습니다.)</Important>
                    <br/>
                    <Important>Linux 운영체제 지원 (검토 중)</Important>
                    <br/>
                    <br/>
                    <br/>
                    [다운로드]
                    <br/>
                    <br/>
                    <Important>다운로드 엔진을 처음부터 재설계하였습니다.</Important>
                    <br/>
                    <Important>기존 다운로드 및 예약 다운로드에서 발생하던 다양한 문제들이 해결되거나 개선되었습니다.</Important>
                    <br/>
                    <br/>
                    - 생방송 인식을 하지 못하거나 느리게 인식하던 현상
                    <br/>
                    - 예약 다운로드의 파일이 분할되는 현상
                    <br/>
                    - 낮은 확률로 생방송이 종료된 이후 별도의 파일에 생방송의 마지막 30초를 다시 다운로드하던 현상
                    <br/>
                    - 낮은 확률로 실시간 다운로드가 도중에 멈추는 현상
                    <br/>
                    - 낮은 확률로 예약 다운로드에서 실제 파일과 함께 빈 파일이 추가로 생성되는 현상
                    <br/>
                    - 낮은 확률로 생방송의 일부가 손실되는 현상
                    <br/>
                    <br/>
                    실시간 다운로드의 안정성을 확보하였습니다.
                    <br/>
                    실시간 다운로드의 손실 확률을 최소화하였으며 네트워크 등의 문제로 생방송에 손실이 있다면 실시간으로 세부 정보를 표시합니다.
                    <br/>
                    이제 실시간 다운로드가 자동으로 생방송 광고를 차단하며 차단된 광고의 구간 수와 길이를 확인할 수 있습니다. (이전 버전에서는 광고 대체화면이 표시되었으나 이제 대체화면도 표시되지 않습니다.)
                    <br/>
                    * 단, 광고가 송출되는 시간 동안의 영상은 다운로드되지 않으며 영상이 중간에 끊어진 것처럼 보일 수 있습니다.
                    <br/>
                    * 이러한 현상을 방지하려면 채널을 구독한 계정 또는 Twitch Turbo 등의 광고 혜택이 있는 계정으로 로그인하세요.
                </TextGroup>
            </div>
        </div>
    );
};

var SourceUrl = "https://github.com/devhotteok/TwitchLink/releases/tag/" + version;

export default {
    UpdateTitle,
    UpdateNote,
    SourceUrl
}