import React from 'react';

import {TextGroup, Important, Title, SubTitle, Line} from '../../components/TextUtils/TextUtils.js';

var version = "3.0.0";

var UpdateTitle = () => {
    return "TwitchLink " + version;
};

var UpdateNote = () => {
    return (
        <div>
            <SubTitle>Update Note</SubTitle>
            <div style={{padding: "30px 10px"}}>
                <TextGroup>
                    <Title>Massive Update</Title>
                    <Line/>
                    [General]
                    <br/>
                    <br/>
                    <Important>We've redesigned the entire core of the application.</Important>
                    <br/>
                    Optimized memory usage.
                    <br/>
                    Software optimized and stabilized.
                    <br/>
                    <Important>Added command support. (You can control TwitchLink using commands.)</Important>
                    <br/>
                    <Important>Linux OS support. (Not confirmed yet.)</Important>
                    <br/>
                    <br/>
                    <br/>
                    [Download]
                    <br/>
                    <br/>
                    <Important>Rebuilt the whole download engine from scratch.</Important>
                    <br/>
                    <Important>Various problems in existing downloads and scheduled downloads have been resolved or improved.</Important>
                    <br/>
                    <br/>
                    - Problems with not recognizing live broadcasts or recognizing them slowly.
                    <br/>
                    - Issue of scheduled downloaded files being split.
                    <br/>
                    - Occasionally re-downloading the last 30 seconds of the live broadcast to a separate file after the live broadcast ended.
                    <br/>
                    - Problem of live downloads halting midway at a low probability.
                    <br/>
                    - Problem of an empty file being additionally generated along with the actual file in scheduled downloads at a low probability.
                    <br/>
                    - Issue of loss occurring in live downloads at a low probability.
                    <br/>
                    <br/>
                    Secured stability of live downloads.
                    <br/>
                    The probability of loss in live downloads is minimized, and detailed information is displayed if the file has loss due to network problems.
                    <br/>
                    Real-time download automatically blocks ads, and you can see the number and duration of blocked ads. (In previous versions, an alternative screen was displayed, but now the alternative screen is no longer displayed.)
                    <br/>
                    * However, the video while the advertisement is running is not downloaded and the video may appear to be cut off in the middle.
                    <br/>
                    * To prevent this from happening, log in with an account that has subscribed to the channel or an account with advertising benefits such as Twitch Turbo.
                </TextGroup>
            </div>
        </div>
    );
};

var SourceUrl = "https://github.com/devhotteok/TwitchLink/releases/tag/" + version;

export default {
    UpdateTitle,
    UpdateNote,
    SourceUrl
}