import React, { Component, Suspense } from 'react';

import './App.css';

import { BrowserRouter, Switch, Route, Redirect } from "react-router-dom";

import ScrollTop from "./utils/scrollTop.js";

import NavBarArea from './layout-components/NavBarArea/NavBarArea.js';
import FooterArea from './layout-components/FooterArea/FooterArea.js';
import Loading from './components/Loading/Loading.js';
import ErrorBoundary from './components/ErrorBoundary/ErrorBoundary.js';
import Versions from "./components/Versions/Versions.js";
import VersionData from "./components/Versions/VersionData.js";


const HomePage = React.lazy(() => import('./pages/HomePage/HomePage.js'));
const DownloadPage = React.lazy(() => import('./pages/DownloadPage/DownloadPage.js'));
const HelpPage = React.lazy(() => import('./pages/HelpPage/HelpPage.js'));
const DonatePage = React.lazy(() => import('./pages/DonatePage/DonatePage.js'));


class App extends Component {
  constructor() {
    super();
    this.state = {
      language: "en"
    };
    this.changeLanguage = (language) => {
      this.setState({language: language});
      var checkUrl = new URL(window.location.href);
      var defaultLang = "en";
      if (language === defaultLang) {
        checkUrl.searchParams.delete("lang");
      }
      else {
        checkUrl.searchParams.set("lang", language);
      }
      window.history.replaceState(null, null, checkUrl.href);
    }
    const searchParams = new URL(window.location.href).searchParams;
    this.popupVersion = searchParams.get("popup");
    if (VersionData.versionList().indexOf(this.popupVersion) === -1) {
      this.popupVersion = undefined;
    }
    else {
      this.popupVersionLang = searchParams.get("lang");
      if (!["en", "ko"].includes(this.popupVersionLang)) {
        this.popupVersionLang = "en";
      }
    }
  }

  T(en, ko) {
    if (this.state.language === "en") {
      return en;
    }
    else {
      return ko;
    }
  }

  render() {
    if (this.popupVersion !== undefined)
      return (<Versions version={this.popupVersion} showMeta={false} language={this.popupVersionLang} />);
    return (
      <div className="App">
        <BrowserRouter basename={process.env.PUBLIC_URL}>
          <ScrollTop/>
          <NavBarArea language={this.state.language} languageChange={this.changeLanguage}/>
          <ErrorBoundary language={this.state.language}>
            <Suspense fallback={
              <Loading>
                {this.T("Loading...", "불러오는 중...")}
              </Loading>
            }>
              <Switch>
                <Route exact path="/" render={() => <HomePage language={this.state.language} />} />
                <Route exact path="/download" render={() => <DownloadPage language={this.state.language} />} />
                <Route exact path="/help" render={() => <HelpPage language={this.state.language} />} />
                <Route exact path="/donate" render={() => <DonatePage language={this.state.language} />} />
                <Redirect to="/" />
              </Switch>
            </Suspense>
          </ErrorBoundary>
          <FooterArea/>
        </BrowserRouter>
      </div>
    );
  }
}

export default App;