import React from "react";

import "./FooterArea.css";

const FooterArea = () => {
  var DEVELOPER = "DevHotteok";
  var FIRST_RELEASE_YEAR = 2021;
  var thisYear = new Date().getFullYear();
  var year = Math.max(thisYear, FIRST_RELEASE_YEAR);
  return (
    <footer className="FooterArea">
      <div>&copy; {year} {DEVELOPER}.</div>
      <div className="contact">
        <div>
          Email: twitchlinkhelp@gmail.com
        </div>
      </div>
    </footer>
  );
};

export default FooterArea;