import React, { Component } from "react";

import { Link } from "react-router-dom";

import './Versions.css';

import Content from '../../components/Contents/index.js';
import Button from '../../components/Buttons/index.js';
import Label from '../../components/Labels/Label/Label.js';

import CopyToClipboard from "../../utils/clipboard.js";

import Icon from '../../components/Icon/Icon.js';

import infoIcon from '../../img/info.svg';

import VersionData from './VersionData.js';

export default class Version extends Component {
  T(en, ko) {
    if (this.props.language === "en") {
      return en;
    }
    else {
      return ko;
    }
  }

  copyLink(url) {
    CopyToClipboard(url);
    alert(this.T("Copied to clipboard", "클립보드에 복사되었습니다."));
  }

  render() {
    const showMeta = this.props.showMeta;
    const CurrentVersionData = VersionData.data[this.props.version];
    const VersionReleased = CurrentVersionData["status"]["released"];
    const VersionNotSupported = !CurrentVersionData["status"]["supported"];
    const UpdateFile = CurrentVersionData["updateNote"][this.props.language];
    const UpdateTitle = UpdateFile.UpdateTitle;
    const UpdateNote = UpdateFile.UpdateNote;
    const SourceUrl = UpdateFile.SourceUrl;
    return (
      <Content.Area className="Versions">
        <Content.Box>
          <Content.Subtitle>
            <UpdateTitle />
          </Content.Subtitle>
          <Content.TextArea>
            <UpdateNote />
          </Content.TextArea>
        </Content.Box>
        {
          showMeta ? (VersionReleased ?
              <div>
                <Content.SrcViewer>
                  {SourceUrl}
                </Content.SrcViewer>
                <Button.ButtonArea>
                  <Button.LinkButton href={SourceUrl} target="_blank">{this.T("Source Code", "소스코드")}</Button.LinkButton>
                  <Button.Button onClick={() => this.copyLink(SourceUrl)}>{this.T("Share", "공유")}</Button.Button>
                  <Link className="Button" style={{color: "black", textDecoration: "none"}} to={"/donate?download=" + this.props.version}>{this.T("Download", "다운로드")}</Link>
                  <Label>{this.T("Version", "버전")}: {this.props.version}</Label>
                </Button.ButtonArea>
                {VersionNotSupported &&
                  <div style={{display: "flex", padding: "5px 15px"}}>
                    <Icon size="small" src={infoIcon} alt="Information" style={{marginRight: "2px"}}/>
                    <Label>
                      {this.T("This version is no longer supported and is not compatible.", "이 버전의 지원이 종료되었으며 호환되지 않습니다.")}
                    </Label>
                  </div>
                }
              </div>
            :
              <div>
                <div style={{display: "flex", padding: "5px 15px"}}>
                  <Icon size="small" src={infoIcon} alt="Information" style={{marginRight: "2px"}}/>
                  <Label>
                    {this.T("This version has not been released yet.", "이 버전은 아직 공개되지 않았습니다.")}
                  </Label>
                </div>
                <Link className="Button supportButton" to={"/donate"}>{this.T("Support", "지원하기")}</Link>
              </div>
            )
          :
            (
              VersionReleased ?
                null
              :
                <div style={{display: "flex", padding: "5px 15px"}}>
                  <Icon size="small" src={infoIcon} alt="Information" style={{marginRight: "2px"}}/>
                  <Label>
                    {this.T("This version has not been released yet.", "이 버전은 아직 공개되지 않았습니다.")}
                  </Label>
                </div>
            )
        }
      </Content.Area>
    );
  }
};